<template>
	<view class="item">
		<view>
			<u-navbar :is-back="true" back-icon-color="white" back-text="返回" :back-text-style="{ color: 'white' }"  title="更改用户名" :background="bgColor" title-color="white" title-size="40" ></u-navbar>
			<u-top-tips ref="uTips" :navbar-height="statusBarHeight + navbarHeight"></u-top-tips>
		</view>
		
		<input v-model="form.usernane" class="username" :style="inputStyle" type="text" @click="inputClick()">
		
		<view class="text">请保管好您的TimeLaw账号哦</view>
		
		<view class="button" :style="buttonStyle" @click="buttonClick()">保存</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				// 状态栏高度，H5中，此值为0，因为H5不可操作状态栏
				statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
				// 导航栏内容区域高度，不包括状态栏高度在内
				navbarHeight: 44,
				form:{
					usernane:''
				},
				inputStyle:'',
				buttonStyle:'',
				bgColor:{
				  "backgroundImage": "linear-gradient(45deg, #1E90FF, 	#BA55D3)"
				}
			}
		},
		onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
			// console.log(option.username); //打印出上个页面传递的参数。
			this.form.usernane = option.username;
		},
		methods:{
			inputClick(){
				this.inputStyle = 'border-bottom: 1px solid #BA55D3';
				this.buttonStyle = 'background-color: #1E90FF; color: white';
			},
			buttonClick(){
				console.log(this.form.usernane);
				
				uni.request({
					url:'http://localhost/users/checkUsername',
					method:"POST",
					data:{
						username: this.form.usernane
					},
					success: (e) => {
						console.log(e);
						if(! e.data.flag){
							this.$refs.uTips.show({
								title: e.data.msg,
								type: 'error'
							});
							this.inputStyle = 'border-bottom: 1px solid red';
							this.form.username = null;
						}else{
							this.inputStyle = 'border-bottom: 1px solid #BA55D3';
							
							uni.request({
								url:'http://localhost/users/updateUsername',
								method:"POST",
								header:{
									token: uni.getStorageSync('token')
								},
								data:{
									username: this.form.usernane
								},
								success: (e) => {
									console.log(e);
									uni.showLoading({
										title: '正在更改'
									});				
									setTimeout(function () {
										//缓存服务器下发token
										uni.setStorageSync('token',e.data.data.token)
										uni.hideLoading();
										uni.navigateBack({
											
										})
									}, 1500);						
								}
							});
						}
					}
				});
			}
		}
	}
</script>

<style scoped>
	.item{
		background-color: white;
		height: 600rpx;
	}
	.username{
		border-bottom: 1px solid #bfbfbf;
		width: 670rpx;
		padding-left: 30rpx;
		font-size: 35rpx;
		margin-top: 70rpx;
		margin-left: 25rpx;
		margin-bottom: 20rpx;
	}
	.text{
		margin-left: 30rpx;
		color: #bfbfbf;
	}
	.button{
		background-color: #dbdbdb;
		width: 200rpx;
		height: 75rpx;
		font-size: 32rpx;
		line-height: 75rpx;
		text-align: center;
		margin-left: 280rpx;
		border-radius: 15rpx;
		color: #8a8a8a;
		margin-top: 150rpx;
	}
</style>